
import {
playerQuery
} from "../js/api"
import querystring from 'vux/src/tools/querystring/index.js'

export default {
name: 'HelloWorld',
data() {
return {
base: '',
url: '132',
showPositionValue: false,
err_msg: '',
resourceList: [],
props: {
key: 'url',
value: 'title'
},
}
},
methods: {
getPlayerQuery() {

let info = {};
playerQuery(info).then(data => {
console.log('传回来的响应信息', data);
let {
err_code,
err_msg,
list
} = data;
if (err_code !== 0) {
this.showPositionValue = true;
this.err_msg = err_msg;
} else {
this.resourceList = list;
this.base = list[0].url;
this.$refs.iframe.src = list[0].url + this.url;
}
});
},
player() {
var hash = window.location.hash;


hash = hash.substr(1);
console.log(this.$router);
console.log(this.url);
this.$router.push({
path: hash,
query: {
url: this.url,
}
});
this.$refs.iframe.src = this.base + this.url;
},
},
mounted() {
this.getPlayerQuery();
},
created() {
var search = window.location.hash.split('?')[1];
this.url = querystring.parse(search).url || '';
}
}



