	import {
		queryPlayerNav,
		queryMovie,
		queryAllItem
	} from "../js/api"
	export default {
		name: 'Menu',
		data() {
			return {
				showPositionValue:false,
				err_msg:'',
				playerNavList:[],
				playerMovieList:[],
				selectedTab:0,
				index:0,
				show:false,
				allItem:[],
				url:'https://v.qq.com',
				showLading:false,
			}
		},
		methods: {
			 log (str) {
      console.log(str)
    },
			clickGridItem(item){
				console.log(item);
				this.getQueryMovie(item.url);
			},
			clickTab(item){
				this.selectedTab=item.id;
				this.getQueryMovie(item.url);
			},
			showEveryItem(item){
				
				console.log(item);
				this.getQueryAllItem(item.href,item);
			},
			getQueryAllItem(url,item) {
				this.showLading=true;
				//获取每一集
				let info = {url:url};
				queryAllItem(info).then(data => {
					console.log('传回来的响应信息', data);
					let {
						err_code,
						err_msg,
						list
					} = data;
					if (err_code !== 0) {
						this.showLading=false;
						this.showPositionValue = true;
						this.err_msg = err_msg;
					} else {
						console.log(list);
						if(list.length===0){
							this.$router.push({
								path:'/home/player',
								query:{
									url:"https:"+item.href
								}
							});
						}else{
							this.allItem = list;
							this.show=true;
							this.showLading=false;
						}
						
					}
				});
			},
			getPlayerNav() {
				this.showLading=true;
				//获取分类
				let info = {};
				queryPlayerNav(info).then(data => {
					console.log('传回来的响应信息', data);
					let {
						err_code,
						err_msg,
						list
					} = data;
					if (err_code !== 0) {
						this.showLading=false;
						this.showPositionValue = true;
						this.err_msg = err_msg;
					} else {
						
						this.playerNavList = list;
						this.getQueryMovie(list[0].url);
						console.log(this.playerNavList);
					}
				});
			},
			getQueryMovie(url) {
				this.showLading=true;
				//获取播放列表
				let info = {url:url};
				queryMovie(info).then(data => {
					console.log('传回来的响应信息', data);
					let {
						err_code,
						err_msg,
						list
					} = data;
					if (err_code !== 0) {
						this.showLading=false;
						this.showPositionValue = true;
						this.err_msg = err_msg;
					} else {
						
						this.playerMovieList = list;
						this.showLading=false;
						console.log(this.playerMovieList);
					}
				});
			},
			player() {
				var hash = window.location.hash;
				// var search=window.location.search;
				// search=search.substr(1);
				hash = hash.substr(1);
				console.log(this.$router);
				console.log(this.url);
				this.$router.push({
					path: hash,
					query: {
						url: this.url,
					}
				});
				this.$refs.iframe.src = this.base + this.url;
			},
			getMovieList(){
				
			},
		},
		mounted() {
			
			this.getPlayerNav();
			
			
		},
		created() {

		}
	}



